import { ElementRef, Injectable } from '@angular/core';

import {
    align,
    applyLocationOffset,
    boundingOffset,
    getWindowViewPort,
    isBodyOffset,
    offset,
    position,
    restrictToView,
    removeScroll,
    scrollPosition,

    AlignSettings,
    BoundingRect,
    ElementRect,
    OffsetPosition,
    PositionSettings,
    ScrollInfo,
    ViewPort

} from '@progress/kendo-popup-common';

import { Position } from '../models/position.interface';

import { isWindowAvailable, scrollableParents } from '../util';

/**
 * @hidden
 */
@Injectable()
export class DOMService {
    public addOffset(current: OffsetPosition, addition: OffsetPosition): OffsetPosition {
        return {
            left: current.left + addition.left,
            top: current.top + addition.top
        } as OffsetPosition;
    }

    public align(settings: AlignSettings): OffsetPosition {
        return align(settings) as OffsetPosition;
    }

    public boundingOffset(el: ElementRef): BoundingRect {
        return boundingOffset(this.nativeElement(el));
    }

    public getWindow(): Window {
        return isWindowAvailable() ? window : null;
    }

    public isBodyOffset(el: ElementRef): boolean {
        return isBodyOffset(this.nativeElement(el));
    }

    public offset(el: ElementRef): ElementRect {
        if (!el) { return null; }

        return offset(this.nativeElement(el));
    }

    public nativeElement(el: ElementRef): HTMLElement {
        if (!el) { return null; }

        return el.nativeElement || el;
    }

    public position(el: ElementRef): ElementRect {
        if (!el) { return null; }

        return position(this.nativeElement(el));
    }

    public relativeOffset(el: ElementRef, currentLocation: OffsetPosition): ElementRect {
        return applyLocationOffset(this.offset(el), currentLocation, this.isBodyOffset(el));
    }

    public removeScroll(rect: ElementRect, scroll: ScrollInfo): ElementRect {
        return removeScroll(rect, scroll) as ElementRect;
    }

    public restrictToView(settings: PositionSettings): Position {
        return restrictToView(settings) as Position;
    }

    public scrollPosition(el: ElementRef): ScrollInfo {
        return scrollPosition(this.nativeElement(el));
    }

    public scrollableParents(el: ElementRef): Array<HTMLElement> {
        return scrollableParents(this.nativeElement(el));
    }

    public windowViewPort(el: ElementRef): ViewPort {
        return getWindowViewPort(this.nativeElement(el));
    }
}



